<template>
    <div class="likelist">
         <div class="outgo-box" @click="goPrenext">
                <van-icon  name="arrow-left" size="30" />
           </div>
        <div class="like-box" v-for="(item,index) in likeList" :key="index" @click="likeLook(item.id)">
            <div class="left">
                <p>{{item.title}}</p>
            </div>
            <div class="right">
                <img :src="item.imges" alt="">
            </div>
        </div>
        <div v-if="falg">
            <van-empty description="收藏列表为空" />
        </div>
    </div>
</template>

<script>
    export default {
        name:'likelist',
        data(){
            return{
                likeList:[],
                falg:false,
            }
        },
        created(){
            let like = JSON.parse(localStorage.getItem("like")) || [];
            this.likeList = like
            // console.log(like);
            if(like.length === 0){
                this.falg = true
            }else{
                this.falg = false
            }

        },
        methods:{
            // 返回上一级
            goPrenext(){
                this.$router.go(-1);
            },
            // 从我的喜欢跳转到详情页面
            likeLook(id){
                // console.log(id);
                this.$router.push({name:'contpage',params:{id}});
            },
        },
    }
</script>

<style lang="less" scoped>

    .likelist{
        width: 100%;
        padding: 5px 0 0 0px;
    }
    .outgo-box{
        width: 60px;
    }
    .like-box{
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 10px;
        .left{
            width: 250px;
            margin-left: 20px;
            p{
                width: 100%;
                font-size: 16px;
                word-wrap:break-word
            }
        }
        .right{
            width: 80px;
            height: 80px;
            margin-right: 10px;
            img{
                display: inline-block;
                width: 100%;
                height: 100%;
                border-radius: 3px;
            }
        }
    }
</style>